// Buttons
.uk-button {
  border-radius: 5px;
}
.uk-button-success {
  background: none;
  color: #fff;
  border:1px solid green;
  &:hover, &:focus {
    background: rgba(0, 128, 0,0.5);
  }
}
.uk-button-primary {
  background: none;
  color: #fff;
  border:1px solid rgb(79, 85, 255);
  &:hover, &:focus {
    background: rgba(79, 85, 255,0.5);
  }
}

.uk-button-danger {
  background: none;
  color: #fff;
  border:1px solid $mrRobotPrimaryColor;
  &:hover, &:focus {
    background: rgba(211, 32, 39, 0.5);
  }
}

// Modal
.uk-modal {
  .uk-modal-dialog {
    background:black;
    -webkit-box-shadow:     -2px 0px 200px -4px rgba(209, 32, 38, 0.5);
    -moz-box-shadow:     -2px 0px 200px -4px rgba(209, 32, 38, 0.5);
    box-shadow:     -2px 0px 200px -4px rgba(209, 32, 38, 0.5);
    border-radius:8px;
    width:640px;
    .uk-modal-header {
      text-align: center;
      background: rgba(255,255,255,0.1);
      color: #ffffff;
      font-weight:lighter;
      letter-spacing: 1.25px;
      border-top-right-radius: 4px;
      border-top-left-radius: 4px;
      text-shadow: $textShadow;
      font-size:1.55em;
    }
    .uk-modal-footer {
      background: none;
      border-bottom-right-radius: 4px;
      border-bottom-left-radius: 4px;
      a {
        padding-top:5px;
        width:100%;
        display:inline-block;
        border-top-right-radius: 0;
        border-top-left-radius: 0;
        font-style: italic;
        color: $mrRobotHoverColor;
        line-height: 1.2em;
        .typed-cursor {
          font-size:1em !important;
          font-style: normal;
          margin-right:4px;
        }
      }
    }
    p {
      text-align: center;
      padding:5px;
    }
  }
}
